

import React, { useState, useCallback, useEffect, useRef } from 'react';
import { removeBackground } from '@imgly/background-removal';
import { DownloadIcon, TrashIcon, SparklesIcon, PictureIcon, BrushIcon, EraserIcon, UndoIcon, XIcon } from './icons.tsx';
import '../index.css';

type Tool = 'erase' | 'restore';

interface BackgroundRemovalModalProps {
  imageFile: File;
  onClose: () => void;
}

const BackgroundRemovalModal: React.FC<BackgroundRemovalModalProps> = ({ imageFile, onClose }) => {
  const [originalFile, setOriginalFile] = useState<File | null>(null);
  const [originalImageUrl, setOriginalImageUrl] = useState<string | null>(null);
  const [processedImageUrl, setProcessedImageUrl] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [progress, setProgress] = useState(0);
  
  const [activeTool, setActiveTool] = useState<Tool | null>(null);
  const [brushSize, setBrushSize] = useState<number>(30);
  const [isPainting, setIsPainting] = useState<boolean>(false);
  const [history, setHistory] = useState<ImageData[]>([]);
  const [lastPos, setLastPos] = useState<{ x: number; y: number } | null>(null);
  
  const editorCanvasRef = useRef<HTMLCanvasElement>(null);
  const brushPreviewCanvasRef = useRef<HTMLCanvasElement>(null);
  const hiddenOriginalCanvasRef = useRef<HTMLCanvasElement>(null);

  const clearCanvasAndHistory = () => {
    setHistory([]);
    [editorCanvasRef, hiddenOriginalCanvasRef, brushPreviewCanvasRef].forEach(ref => {
      const canvas = ref.current;
      if (canvas) {
        const ctx = canvas.getContext('2d');
        ctx?.clearRect(0, 0, canvas.width, canvas.height);
      }
    });
  };
  
  const handleReset = useCallback(() => {
    if (originalImageUrl) URL.revokeObjectURL(originalImageUrl);
    if (processedImageUrl) URL.revokeObjectURL(processedImageUrl);
    setOriginalFile(null);
    setOriginalImageUrl(null);
    setProcessedImageUrl(null);
    setError(null);
    setIsLoading(false);
    setActiveTool(null);
    setProgress(0);
    setLastPos(null);
    clearCanvasAndHistory();
    onClose();
  }, [originalImageUrl, processedImageUrl, onClose]);

  useEffect(() => {
    return () => {
      if (originalImageUrl) URL.revokeObjectURL(originalImageUrl);
      if (processedImageUrl) URL.revokeObjectURL(processedImageUrl);
    };
  }, [originalImageUrl, processedImageUrl]);

  useEffect(() => {
    if (!processedImageUrl || !editorCanvasRef.current || !brushPreviewCanvasRef.current) return;
    const canvas = editorCanvasRef.current;
    const previewCanvas = brushPreviewCanvasRef.current;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const img = new Image();
    img.crossOrigin = "anonymous";
    img.src = processedImageUrl;
    img.onload = () => {
      canvas.width = img.naturalWidth;
      canvas.height = img.naturalHeight;
      previewCanvas.width = img.naturalWidth;
      previewCanvas.height = img.naturalHeight;
      
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(img, 0, 0);

      const initialState = ctx.getImageData(0, 0, canvas.width, canvas.height);
      setHistory([initialState]);
    };
  }, [processedImageUrl]);

  useEffect(() => {
    if (!originalFile) return;
    const canvas = hiddenOriginalCanvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;
    
    const originalUrl = URL.createObjectURL(originalFile);
    const img = new Image();
    img.src = originalUrl;
    img.onload = () => {
        canvas.width = img.naturalWidth;
        canvas.height = img.naturalHeight;
        ctx.drawImage(img, 0, 0);
        URL.revokeObjectURL(originalUrl);
    }
  }, [originalFile]);

  const processImage = useCallback(async (file: File) => {
    if (!file) return;

    setIsLoading(true);
    setError(null);
    setProgress(0);
    
    const originalUrl = URL.createObjectURL(file);
    setOriginalFile(file);
    setOriginalImageUrl(originalUrl);

    try {
      const blob = await removeBackground(file, {
          progress: (key, current, total) => {
              const percentage = Math.round((current / total) * 100);
              setProgress(percentage);
          }
      });
      const url = URL.createObjectURL(blob);
      setProcessedImageUrl(url);
    } catch (e: any) {
      console.error(e);
      setError(`Falha ao processar a imagem: ${e.message}`);
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    processImage(imageFile);
  }, [imageFile, processImage]);

  const handleDownload = () => {
    const canvas = editorCanvasRef.current;
    if (!canvas) return;
    canvas.toBlob((blob) => {
        if (!blob) return;
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `sem-fundo-editado-${originalFile?.name || 'imagem.png'}`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
    }, 'image/png');
  };

  const getCanvasCoordinates = (e: React.MouseEvent<HTMLDivElement>) => {
    const canvas = editorCanvasRef.current;
    if (!canvas) return null;
    const rect = canvas.getBoundingClientRect();
    const scaleX = canvas.width / rect.width;
    const scaleY = canvas.height / rect.height;
    return {
      x: (e.clientX - rect.left) * scaleX,
      y: (e.clientY - rect.top) * scaleY,
    };
  };

  const draw = useCallback((x1: number, y1: number, x2: number, y2: number) => {
    const canvas = editorCanvasRef.current;
    const ctx = canvas?.getContext('2d');
    const originalCanvas = hiddenOriginalCanvasRef.current;
    if (!ctx || !activeTool || !originalCanvas) return;
    
    ctx.lineCap = 'round';
    ctx.lineJoin = 'round';
    ctx.lineWidth = brushSize;

    if (activeTool === 'erase') {
      ctx.globalCompositeOperation = 'destination-out';
      ctx.beginPath();
      ctx.moveTo(x1, y1);
      ctx.lineTo(x2, y2);
      ctx.stroke();
    } else if (activeTool === 'restore') {
      ctx.globalCompositeOperation = 'source-over';
      ctx.beginPath();
      ctx.moveTo(x1, y1);
      ctx.lineTo(x2, y2);
      ctx.stroke();
      
      ctx.globalCompositeOperation = 'source-in';
      
      ctx.drawImage(originalCanvas, 0, 0);
    }

    ctx.globalCompositeOperation = 'source-over';
  }, [activeTool, brushSize]);

  const handleMouseDown = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!activeTool) return;
    const coords = getCanvasCoordinates(e);
    if (coords) {
        setIsPainting(true);
        setLastPos(coords);
        draw(coords.x, coords.y, coords.x, coords.y);
    }
  };
  
  const handleMouseUp = useCallback(() => {
    if (!isPainting) return;
    setIsPainting(false);
    setLastPos(null);
    
    const canvas = editorCanvasRef.current;
    const ctx = canvas?.getContext('2d');
    if (ctx) {
        const newImageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        setHistory(prev => [...prev, newImageData]);
    }
  }, [isPainting]);

  useEffect(() => {
    window.addEventListener('mouseup', handleMouseUp);
    return () => window.removeEventListener('mouseup', handleMouseUp);
  }, [handleMouseUp]);
  
  const drawBrushPreview = (x: number, y: number) => {
      const canvas = brushPreviewCanvasRef.current;
      const ctx = canvas?.getContext('2d');
      if (!ctx || !activeTool) return;

      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.beginPath();
      ctx.arc(x, y, brushSize / 2, 0, Math.PI * 2);
      ctx.strokeStyle = activeTool === 'restore' ? 'rgba(34, 197, 94, 0.9)' : 'rgba(239, 68, 68, 0.9)';
      ctx.lineWidth = 2;
      ctx.stroke();
  };

  const clearBrushPreview = () => {
      const canvas = brushPreviewCanvasRef.current;
      const ctx = canvas?.getContext('2d');
      ctx?.clearRect(0, 0, canvas.width, canvas.height);
  };

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const coords = getCanvasCoordinates(e);
    if (!coords) return;
    
    drawBrushPreview(coords.x, coords.y);

    if (isPainting && activeTool && lastPos) {
      draw(lastPos.x, lastPos.y, coords.x, coords.y);
      setLastPos(coords);
    }
  };

  const handleMouseLeave = () => {
      clearBrushPreview();
  };
  
  const handleUndo = () => {
    if (history.length <= 1) return;

    const newHistory = history.slice(0, -1);
    setHistory(newHistory);

    const stateToRestore = newHistory[newHistory.length - 1];
    const canvas = editorCanvasRef.current;
    const ctx = canvas?.getContext('2d');
    if (ctx && stateToRestore) {
        ctx.putImageData(stateToRestore, 0, 0);
    }
  };

  const toggleTool = (tool: Tool) => {
      setActiveTool(current => (current === tool ? null : tool));
  }

  return (
    <div className="fixed inset-0 bg-black/70 z-50 flex items-center justify-center p-4 animate-fade-in" onClick={handleReset}>
        <div className="bg-white dark:bg-slate-800 rounded-2xl shadow-2xl w-full max-w-6xl animate-slide-up" onClick={e => e.stopPropagation()}>
           <div className="p-4 md:p-6">
                <button onClick={handleReset} className="absolute top-4 right-4 text-slate-500 hover:text-slate-800 dark:hover:text-slate-200 transition-colors z-10">
                    <XIcon className="w-7 h-7" />
                </button>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 md:gap-6">
                    <div className="flex flex-col items-center">
                        <h3 className="text-lg font-semibold mb-3 flex items-center"><PictureIcon className="w-5 h-5 mr-2"/>Original</h3>
                        <div className="w-full aspect-square bg-slate-100 dark:bg-slate-900/50 rounded-lg overflow-hidden flex items-center justify-center shadow-inner">
                            {originalImageUrl && <img src={originalImageUrl} alt="Original" className="w-full h-full object-contain" />}
                        </div>
                    </div>
                    <div className="flex flex-col items-center">
                        <h3 className="text-lg font-semibold mb-3 flex items-center"><SparklesIcon className="w-5 h-5 mr-2 text-sky-400"/>Resultado Editável</h3>
                        <div
                            className="w-full aspect-square bg-slate-200/50 dark:bg-slate-900/50 rounded-lg overflow-hidden flex items-center justify-center shadow-inner relative"
                            style={{backgroundImage: 'url("data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 32 32\' width=\'32\' height=\'32\' fill=\'none\'%3e%3cpath d=\'M0 0H16V16H0V0ZM16 16H32V32H16V16Z\' fill=\'%23e2e8f0\'/%3e%3cpath d=\'M16 0H32V16H16V0ZM0 16H16V32H0V16Z\' fill=\'%23d1d5db\'/%3e%3c/svg%3e")'}}
                            onMouseDown={handleMouseDown} onMouseMove={handleMouseMove} onMouseLeave={handleMouseLeave}
                        >
                            {isLoading && ( 
                              <div className="w-3/4 text-center">
                                  <div className="bg-slate-300 dark:bg-slate-700 rounded-full h-2.5">
                                      <div className="bg-indigo-600 h-2.5 rounded-full" style={{ width: `${progress}%`, transition: 'width 0.3s ease-in-out' }}></div>
                                  </div>
                                  <p className="mt-2 text-sm text-slate-500 dark:text-slate-400">Processando... {progress}%</p>
                              </div> 
                            )}
                            {error && <p className="text-red-500 text-center p-4">{error}</p>}
                            {!isLoading && !error && processedImageUrl && (
                              <>
                                <canvas ref={editorCanvasRef} className={`w-full h-full object-contain ${activeTool ? 'cursor-none' : ''}`} />
                                <canvas ref={brushPreviewCanvasRef} className="absolute top-0 left-0 w-full h-full object-contain pointer-events-none" />
                              </>
                            )}
                        </div>
                    </div>
                </div>
                
                {processedImageUrl && !isLoading && (
                    <div className="mt-6 p-4 bg-slate-100 dark:bg-slate-800 rounded-lg flex flex-col md:flex-row gap-6 items-center justify-center">
                        <div className="flex gap-2">
                          <button onClick={handleUndo} disabled={history.length <= 1} title="Desfazer" className="p-3 rounded-lg transition-colors bg-white/10 hover:bg-white/20 disabled:opacity-50 disabled:cursor-not-allowed">
                              <UndoIcon className="w-6 h-6"/>
                          </button>
                          <button onClick={() => toggleTool('restore')} title="Restaurar" className={`p-3 rounded-lg transition-colors ${activeTool === 'restore' ? 'bg-green-500 text-white shadow-lg' : 'bg-white/10 hover:bg-white/20'}`}>
                              <BrushIcon className="w-6 h-6"/>
                          </button>
                          <button onClick={() => toggleTool('erase')} title="Borracha" className={`p-3 rounded-lg transition-colors ${activeTool === 'erase' ? 'bg-red-500 text-white shadow-lg' : 'bg-white/10 hover:bg-white/20'}`}>
                              <EraserIcon className="w-6 h-6"/>
                          </button>
                        </div>
                        <div className="flex items-center gap-3 w-full md:w-auto">
                          <label htmlFor="brushSize" className="text-sm font-medium whitespace-nowrap">Tamanho:</label>
                          <input id="brushSize" type="range" min="2" max="100" value={brushSize} onChange={(e) => setBrushSize(Number(e.target.value))} className="w-full h-2 bg-slate-200 dark:bg-slate-700 rounded-lg appearance-none cursor-pointer" />
                          <span className="text-sm w-8 text-center">{brushSize}</span>
                        </div>
                    </div>
                )}
                
                <div className="mt-6 flex flex-col sm:flex-row items-center justify-center gap-4">
                    <button onClick={handleDownload} disabled={!processedImageUrl || isLoading || !!error} className="w-full sm:w-auto flex items-center justify-center gap-2 bg-green-600 text-white font-semibold px-6 py-3 rounded-lg hover:bg-green-700 transition-colors shadow-md hover:shadow-lg disabled:bg-slate-400 disabled:dark:bg-slate-600 disabled:cursor-not-allowed disabled:shadow-none">
                        <DownloadIcon className="w-5 h-5" /> Download
                    </button>
                    <button onClick={handleReset} className="w-full sm:w-auto flex items-center justify-center gap-2 bg-slate-600 text-white font-semibold px-6 py-3 rounded-lg hover:bg-slate-700 transition-colors shadow-md hover:shadow-lg">
                        <TrashIcon className="w-5 h-5" /> Escolher Outra
                    </button>
                </div>
            </div>
        </div>
        <canvas ref={hiddenOriginalCanvasRef} style={{ display: 'none' }} />
    </div>
  );
};

export default BackgroundRemovalModal;

