import React from 'react';
import { createRoot } from 'react-dom/client';
import BackgroundRemovalModal from './components/BackgroundRemovalModal.tsx';

export { BackgroundRemovalModal };

export const openBackgroundRemovalModal = (imageFile: File, onClose: () => void) => {
  const container = document.createElement('div');
  document.body.appendChild(container);
  const root = createRoot(container);

  const handleClose = () => {
    root.unmount();
    document.body.removeChild(container);
    onClose();
  };

  root.render(
    <React.StrictMode>
      <BackgroundRemovalModal imageFile={imageFile} onClose={handleClose} />
    </React.StrictMode>
  );
};
